@import url(https://fonts.googleapis.com/css?family=Heebo&display=swap);
.header{width:100%;height:72px;background-color:#1e1e1e;box-shadow:0px 2px 4px #0a0a0a;position:relative}.header__title{position:absolute;font-size:2.4em;color:rgba(127,252,127,0.541);margin-bottom:0;margin-top:0;padding-top:8px;padding-bottom:8px;left:4%}.header__link{position:absolute;font-size:2.4em;color:rgba(127,252,127,0.541);right:4%;margin-bottom:0;margin-top:0;padding-top:8px;padding-bottom:8px}.header__link:hover{color:rgba(127,252,127,0.753)}.footer{color:white;height:60px;width:100%;align-items:center;background-color:#121212;box-shadow:0 -2px 4px #0a0a0a;text-align:center}.footer__container{height:100%;display:flex;justify-content:center;align-items:center}.footer__content{color:rgba(127,252,127,0.541);font-size:1.4em;margin:0 30px}.footer__link{color:rgba(127,252,127,0.541)}.footer__link::before{display:inline-block;content:"  "}.footer__link:hover{color:rgba(127,252,127,0.753)}@media only screen and (max-width: 900px){.footer__content{font-size:1.2em}}@media only screen and (max-width: 550px){.footer__content{font-size:1.1em;margin:0 28px}}@media only screen and (max-width: 440px){.footer{height:80px}.footer__content{font-size:1em;margin:0 24px}}.main{height:calc(100vh - 132px);position:relative}@media only screen and (max-width: 435px){.main{height:calc(100vh - 152px);min-height:400px}}.map{position:absolute;height:52%;width:55%;right:4%;bottom:5%;box-shadow:2px 2px 4px #0a0a0a;border-radius:9px}.map__content{position:absolute;height:100%;width:100%;box-sizing:border-box;z-index:0;border-radius:inherit}.map__curtain{position:absolute;height:100%;width:100%;box-sizing:border-box;z-index:1;opacity:0.6;background-color:whitesmoke;border-radius:inherit}.map__explanation{margin:0;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);text-align:center;font-size:1.4em}.map .invisible{display:none}@media only screen and (max-width: 900px){.map{height:53.7%}}@media only screen and (max-width: 600px){.map{width:calc(100% - 4% * 2);height:46%}}.brewery-data{position:absolute;height:33%;width:55%;top:5%;right:4%;background-color:#1e1e1e;box-shadow:2px 2px 4px #0a0a0a;color:rgba(127,252,127,0.541);text-align:center;border-radius:9px;display:flex;justify-content:center;flex-direction:column}.brewery-data__paragraph{font-size:1.2em;padding-left:10%;padding-right:10%;margin:17px 0}.brewery-data__link{display:inline;font-size:1.2em;color:rgba(127,252,127,0.541);margin:17px 0}.brewery-data__link:hover{color:rgba(127,252,127,0.753)}@media only screen and (max-width: 900px){.brewery-data{height:34%}}@media only screen and (max-width: 600px){.brewery-data{width:calc(100% - 4% * 2);top:12%}.brewery-data__paragraph{font-size:1em;padding-left:7%;padding-right:7%;margin:6px 0}.brewery-data__link{font-size:1em;margin:6px 0}}.suggestions{position:absolute;top:10%;left:4%;width:35%;height:85%;box-sizing:border-box;border-radius:9px;z-index:5}.suggestions--invisible{display:none}.suggestions__list{padding:0;margin:0;width:100%;height:100%;list-style-type:none}.suggestions__item{height:6.66%;box-shadow:2px 0px 4px #0a0a0a}.suggestions__item:last-child{border-radius:0 0 9px 9px;box-shadow:2px 2px 4px #0a0a0a}.suggestions__item:first-child{box-shadow:2px 2px 4px #0a0a0a}.suggestions__button{color:rgba(127,252,127,0.541);background-color:#181818;font-family:"Heebo", sans-serif;border:none;width:100%;height:100%;border-radius:inherit;text-align:left;text-overflow:ellipsis;white-space:nowrap;overflow:hidden}.suggestions__button:hover,.suggestions__button:focus{color:rgba(127,252,127,0.753);background-color:#1e1e1e}@media only screen and (max-width: 600px){.suggestions{width:calc(100% - 4% * 2)}}.input{display:block;position:absolute;top:5%;left:4%;width:35%;height:5%;box-sizing:border-box;color:rgba(127,252,127,0.541);font-family:"Heebo", sans-serif;background-color:#181818;text-indent:6px;border:0;border-radius:9px;font-size:1.2em;box-shadow:2px 2px 4px #0a0a0a}.input::-webkit-input-placeholder{color:rgba(127,252,127,0.541)}.input: